body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background-color: black;
}

.brand {
    list-style: none;
    font-size: 4rem;
    font-family: 'Metal Mania', cursive;
    letter-spacing: 2px;
    color: white;
}
    .sub-brand {
        color: white;
        list-style: none;
    }

.iframe-container {
    position: relative;
    width: 100%;
    padding-bottom: 56.25%; 
    /* height: 0; */
}
    .iframe-container iframe {
        position: absolute;
        top:0;
        left: 0;
        width: 80%;
        height: 80%;
        padding-left: 12%;
    }

.list-item {
    list-style: none;
    text-decoration: none;
    color: white;
    text-align: center;
}

.nav-list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 10px;
    width: 100%;
    text-align: center;
}

ul {
    padding: 0;
}
 


